import React from 'react';
import { Menu } from '../components/menu';
import Footer from '../components/footer';
import Integrante from '../components/integrante';
import Integrante2 from '../components/integrante2';
import styled from 'styled-components';
import Daniela from '../assets/Daniela.png'
import Carla from '../assets/Carla.png'
import Luna from '../assets/Luna.png'
import Jamille from '../assets/Jamille.png'
import Alana from '../assets/Alana.png'
import Enzo from '../assets/Enzo.png'
import Unisinos from "../assets/unisinos.jpeg"
import Antonia from "../assets/Antonia.png"
import Laura from "../assets/Laura.png"
import Luciani from "../assets/Luciani.png"

const Container2 = styled.div`
    display: flex;
    flex-direction: column;
    padding: 6vw 4vw;
    gap: 2vw;
    justify-content: center;
    background-image: url(${Unisinos});
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5); /* Preto fosco com opacidade */
        z-index: 1;
    }

    > * {
        position: relative;
        z-index: 2;
    }

          @media (max-width: 768px) {
    gap: 4vw;
  }
`;

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 4vw 0;
    background: white;

      @media (max-width: 768px) {
    gap: 6vw;
    padding: 12vw 0;
  }
`

const ContainerBotao = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 1vw;

          @media (max-width: 768px) {
    gap: 4vw;
  }
`

const Botao = styled.button`
    background: #4D69FB;
    color: white;
    border-radius: 10px;
    padding: 1vw;
    transition: transform 0.3s ease-in-out; /* Transio suave para o aumento de tamanho */

      &:hover {
    transform: scale(1.05); /* Aumenta o tamanho do continer em 5% */
  }
`

const Titulo = styled.p`
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  color: white;
  font-size: 40px;
  text-align: center;
  text-decoration: underline;

            @media (max-width: 768px) {
     font-size: 30px;
  }
`;

function Sobrenos() {
  return (
    <>
      <Menu />
      <Container2>
        <Titulo>Sobre ns</Titulo>
        <ContainerBotao>
          <Botao>Cdigo de tica</Botao>
          <Botao>Manifesto de Conduta</Botao>
          <Botao>Poltica de Publicaes</Botao>
        </ContainerBotao>
      </Container2>
      <Container>
        <Integrante nome='Professora Dra. Daniela Pellin'
          texto='Professora, Pesquisadora, Doutora e Ps Doutora do Programa de Mestrado Profissional em Direito da Empresa e dos Negcios da UNISINOS, Daniela  lder do Grupo de Pesquisa CUBOTECH/CNPq.'
          imagem={Daniela}
          link='http://lattes.cnpq.br/8962572217907641' />
        <Integrante2 nome='Enzo Bitencourt'
          texto='Aluno pesquisador e graduando em Relaes Internacionais pela Unisinos, Enzo  tcnico em informtica e responsvel por toda a parte relacionada a tecnologia do Grupo de Pesquisa CUBOTECH/CNPq.'
          imagem={Enzo}
          link='http://lattes.cnpq.br/5697554593364315' />
        <Integrante nome='Alana Bortolini'
          texto='Aluna de Relaes Internacionais pela UNISINOS, Alana  responsvel pela pesquisa que busca analisar as polticas e diretrizes relacionadas aos princpios ESG promovidas
por organismos internacionais. Alm disso, ela   responsvel pelas mdias sociais do grupo CUBOTECH/CNPq.'
          imagem={Alana}
          link='' />
        <Integrante2 nome='Luna Marengo Tesser'
          texto='Luna  atualmente aluna do segundo semestre da faculdade de Direito da Unisinos, seu projeto de pesquisa aborda o impacto da tecnologia jurdica e big data no ambiente jurdico. Alm disso,  a responsvel pela edio e criao de vdeos e podcasts do grupo CUBOTECH/CNPq.'
          imagem={Luna}
          link='https://lattes.cnpq.br/7206189569552159' />
        <Integrante nome='Carla Susin'
          texto='Estudante do oitavo semestre de Direito pela UNISINOS, a principal rea de pesquisa de Carla tem foco em governana corporativa, que objetiva a elaborao de um caderno de diretrizes para a adoo da tica da responsabilidade nas MPEs.'
          imagem={Carla}
          link='' />
        <Integrante2 nome='Jamille Nejar'
          texto='Aluna do oitavo semestre da graduao em Direito pela UNISINOS, o principal objeto da pesquisa de Jamille  baseado no processo de construo jurdica das concesses e PPPs  luz da agenda 2030 e dos Direitos Humanos.'
          imagem={Jamille}
          link='' />
        <Integrante nome='Antnia Kellermann'
          texto='Acadmica de Direito na UNISINOS e pesquisadora, Antonia concentra seus estudos na interseo entre polticas pblicas, sustentabilidade e desenvolvimento socioeconmico. Suas pesquisas exploram a colaborao internacional e o impacto na legislao brasileira, com foco em temas que envolvem governana pblica e prticassustentveis.'
          imagem={Antonia}
          link='http://lattes.cnpq.br/0473265581411672' />
        <Integrante2 nome='Laura Rossi Rimolo'
          texto='Aluna de Direito-Les, no 8o semestre da Unisinos/POA. Laura  responsvel por realizar pesquisas envolvendo ESG, com destaque para os processos de Parcerias Pblico Privadas (PPPs), com nfase na responsabilidade trabalhista e sustentveldasempresas.'
          imagem={Laura}
          link='http://lattes.cnpq.br/3212880532214441' />
          <Integrante nome='Luciani Gabrieli Colares Lopes'
          texto='Aluna de Direito no 8 semestre da Unisinos, em Porto Alegre. Com uma trajetria acadmica marcada pelo compromisso com o Direito e a sustentabilidade, Luciani dedica-se  pesquisa na rea de Direito Regulatrio e Desenvolvimento Econmico Sustentvel. Com uma viso crtica e inovadora, ela busca contribuir para a construo de um futuro mais justo e sustentvel pormeiodoDireito.'
          imagem={Luciani}
          link='http://lattes.cnpq.br/6104038500979127' />
      </Container>
      <Footer />
    </>
  );
}

export default Sobrenos;
