import Footer from "../components/footer"
import { Menu } from "../components/menu"
import Daniela from "../assets/Daniela1.jpg"
import styled from "styled-components"
import { Image } from "@chakra-ui/react";

const Container3 = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

const Titulo = styled.p`
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  color: #2B2C34;
  font-size: 45px;

    @media (max-width: 768px) {
          font-size: 40px;
          line-height: 120%;
          margin-bottom: 3vw;
  }
`;

const Nome = styled.p`
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-weight: 500;
  color: #2B2C34;
  font-size: 12px;
`;

const Infos = styled.p`
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #2B2C34;
  font-size: 10px;
`;

const Autor = styled.div`
    display: flex;
    flex-direction: row;
    gap: 1vw;
    align-items: center;
`

const Container = styled.div`
    display: flex;
    flex-direction: column;
    background: white;
    padding: 10vh 25%;
    gap: 2vw;

    @media (max-width: 768px) {
        padding: 6vh 10%;
        gap: 6vw;
  }
`

const Lide = styled.p`
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #9a9494;
  font-size: 14px;
  
`;

const Fonte = styled.p`
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: black;
  font-size: 12px;
  
`;

const Texto = styled.p`
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: black;
  font-weight: 300;
  font-size: 16px;

`;

const Divisao = styled.div`
    display:flex;
    flex-direction: column;
    gap: 1vw;
`


function BlogArticle() {
    return (
        <>
            <Menu />
            <Container>
                <div>
                    <Titulo>Normas ABNT e Pesquisa</Titulo>
                    <Lide>A aplicao rigorosa das normas ABNT  fundamental para a credibilidade e padronizao das pesquisas acadmicas no Brasil. Estas normas asseguram a qualidade e a uniformidade dos trabalhos cientficos, facilitando a compreenso e a disseminao do conhecimento, alm de serem essenciais para a validao e aceitao dos estudos em mbito nacional e internacional.</Lide>
                </div>
                <Autor>
                    <Image
                        src={Daniela}
                        borderRadius="50%"
                        width="50px"
                        height='50px'
                        objectFit='cover'
                        objectPosition='center'
                    />
                    <Container3>
                        <Nome>Daniela Pellin</Nome>
                        <Infos>20/08/2024</Infos>
                    </Container3>
                </Autor>
                <Divisao>
                    <Image
                        src='https://matriculas.estacio.br/blog/wp-content/uploads/2020/05/29est-biblioteca.jpg'
                        borderRadius="5px"
                        width="100%"
                        height='100%'
                        objectFit='cover'
                        objectPosition='center'
                    />
                    <Fonte><b>Fonte:</b> Faculdade Estcio</Fonte>
                </Divisao>
                <Texto>As normas da Associao Brasileira de Normas Tcnicas (ABNT) so fundamentais para a produo cientfica no Brasil, desempenhando um papel crucial na padronizao e na apresentao dos trabalhos acadmicos. A aplicao dessas normas vai alm da esttica; ela  essencial para garantir a credibilidade e a uniformidade das pesquisas. Um trabalho acadmico que segue rigorosamente as diretrizes da ABNT demonstra profissionalismo e cuidado, aspectos que so indispensveis na academia.

                    <br /><br />Alm de conferir seriedade, as normas ABNT facilitam a leitura e a compreenso dos textos cientficos. Pesquisadores e estudantes de diferentes reas, familiarizados com a estrutura padronizada, conseguem entender e analisar os contedos de forma mais eficiente. A formatao correta das sees, como introduo, metodologia, resultados e concluso,  crucial para que o leitor siga o raciocnio do autor de maneira clara e objetiva.
                    <br /><br />Outro ponto importante  a contribuio das normas ABNT para a tica na pesquisa. A formatao adequada de citaes e referncias impede o plgio, assegurando que as ideias e os dados de outros autores sejam devidamente creditados. Isso no s respeita o trabalho de outros pesquisadores, mas tambm refora a integridade do prprio trabalho acadmico.
                    <br /><br />No contexto das publicaes cientficas, a aderncia s normas ABNT  frequentemente um requisito para a aceitao de artigos em peridicos e conferncias. A falta de conformidade com essas normas pode levar  rejeio de um trabalho, independentemente da qualidade de seu contedo. Portanto,  vital que os pesquisadores dominem essas regras para garantir que seus estudos sejam bem recebidos e disseminados.
                    <br /><br />Embora sejam essenciais, as normas ABNT podem ser desafiadoras, especialmente para aqueles que esto comeando na pesquisa. A complexidade das regras, que envolvem detalhes como margens, espaamento, e a formatao de tabelas, pode ser difcil de dominar. Felizmente, existem recursos como modelos pr-formatados e tutoriais que podem ajudar na correta aplicao das normas.
                    <br /><br />Em resumo, as normas ABNT so um elemento indispensvel na estruturao da pesquisa acadmica no Brasil. Elas garantem a clareza, a organizao e a credibilidade dos trabalhos cientficos, tornando-os mais acessveis e respeitados na comunidade acadmica. Investir tempo para aprender e aplicar essas normas  essencial para o sucesso na carreira acadmica e para a contribuio efetiva ao conhecimento cientfico.
                </Texto>
            </Container>
            <Footer />
        </>
    )
}

export default BlogArticle